/* via https://fonts.googleapis.com/css?family=Nunito:300 */
:root {
  --fontWeight-nunito-light: 300;
}

@font-face {
  font-family: 'Nunito';
  font-style: normal;
  font-weight: var(--fontWeight-nunito-light);
  src: local('Nunito-Light'), url(https://fonts.gstatic.com/s/nunito/v7/1TiHc9yag0wq3lDO9cw0vpBw1xU1rKptJj_0jans920.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}
