/* via https://fonts.googleapis.com/css?family=Nunito:400 */
:root {
  --fontWeight-nunito-regular: 400;
}

@font-face {
  font-family: 'Nunito';
  font-style: normal;
  font-weight: var(--fontWeight-nunito-regular);
  src: local('Nunito-Regular'), url(https://fonts.gstatic.com/s/nunito/v7/6TbRXKWJjpj6V2v_WyRbMevvDin1pK8aKteLpeZ5c0A.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}
